  .gradient {
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab, #a3ffea);
    background-size: 400% 400%;
    -webkit-animation: gradient 12s ease infinite;
            animation: gradient 12s ease infinite;
