    height: 100%;
    cursor: pointer;
   
  }
  
  path:hover,
  circle:hover {
    stroke: #227da7 !important;
    stroke-width: 5px;
    stroke-linejoin: round;
    fill: grey!important;
    cursor: pointer;
  }
  
