  /* left: 160px; */
  opacity: 1;
}

.inner-header {
  font-size: 45px;
  height: 65vh;
  width: 100%;
  margin: 0;
  padding: 0;
}

.inner-header flex {
  font-size: 600px;
}

.flex {
  /*Flexbox for containers*/
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.waves {
  position: static;
  max-width: 100%;
  height: 15vh;
  margin-bottom: -7;
  /*Fix for safari gap*/
  min-height: 100px;
  max-height: 150px;
}

.content {
  position: relative;
  height: 20vh;
  text-align: center;
  background-color: white;
}

/* Waves Animation */

.parallax>use {
  animation: move-forever 25s cubic-bezier(.55, .5, .45, .5) infinite;
}

.parallax>use:nth-child(1) {
  animation-delay: -2s;
  animation-duration: 7s;
}

.parallax>use:nth-child(2) {
  animation-delay: -3s;
  animation-duration: 10s;
}

.parallax>use:nth-child(3) {
  animation-delay: -4s;
  animation-duration: 13s;
}

.parallax>use:nth-child(4) {
  animation-delay: -5s;
  animation-duration: 20s;
}

@keyframes move-forever {
  0% {
    transform: translate3d(-90px, 0, 0);
  }

  100% {
    transform: translate3d(85px, 0, 0);
  }
}

/*Shrinking for mobile*/

@media only screen and (min-width: 200px) and (max-width: 1000px) {
    .waves {
      height: 80px;
      min-height: 40px;
    }


  .content {
    height: 30vh;
  }

  h1 {
    font-size: 24px;
  }
}